import { _decorator, CharacterController, Component, input, Input, EventKeyboard, KeyCode, Node, v3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('CatController')
export class CatController extends Component {
    speed = v3(0, 0, 0);
    g = 9.8;

    characterController: CharacterController | null = null;
    onLoad() {
        input.on(Input.EventType.KEY_DOWN, this.onKeyDown, this);
        input.on(Input.EventType.KEY_UP, this.onKeyUp, this);
    }

    onDestroy() {
        input.off(Input.EventType.KEY_DOWN, this.onKeyDown, this);
        input.off(Input.EventType.KEY_UP, this.onKeyUp, this);
    }

    onKeyDown(event: EventKeyboard) {
        switch (event.keyCode) {
            case KeyCode.KEY_A:
                this.speed.z = -1;
                break;
            case KeyCode.KEY_D:
                this.speed.z = 1;
                break;
            case KeyCode.KEY_W:
                this.speed.x = 1;
                break;
            case KeyCode.KEY_S:
                this.speed.x = -1;
                break;
            case KeyCode.SPACE:
                if (this.characterController?.isGrounded) {
                    this.speed.y = 5;
                }
                break;
        }
    }

    onKeyUp(event: EventKeyboard) {
        switch (event.keyCode) {
            case KeyCode.KEY_A:
                this.speed.z = 0;
                break;
            case KeyCode.KEY_D:
                this.speed.z = 0;
                break;
            case KeyCode.KEY_W:
                this.speed.x = 0;
                break;
            case KeyCode.KEY_S:
                this.speed.x = 0;
                break;
        }
    }

    start() {
        this.characterController = this.node.getComponent(CharacterController);

    }

    update(deltaTime: number) {
        let pos = this.characterController.centerWorldPosition;

        // if (this.characterController?.isGrounded === false) {
        //     this.speed.y -= this.g * deltaTime;
        // }
        let speed = this.speed.clone();
        if (speed.length() == 0) {
            return;
        }
        console.log(this.characterController?.isGrounded, this.name, pos.x, pos.y, pos.z, this.speed.x, this.speed.y, this.speed.z);
        this.characterController?.move(speed.multiplyScalar(5 * deltaTime));
    }
}
